@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Readex+Pro:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Readex+Pro:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300&display=swap);

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    padding: 0;
    box-sizing: border-box;
}
.nav-link{
    display: flex;
    justify-content: flex-end;
    font-family: 'Outfit', sans-serif;

}
.navbar-brand{
    font-size: 1.9em !important;
    color: #fff !important;
}
#navbarSupportedContent{
    display: flex;
    justify-content: flex-end;
}
.container-fluid{
    background-color: #108dcc;
    position: fixed;
    top: 0;
    z-index: 10000;
}
.App-header {
  font-size: calc(10px + 2vmin);
}
.App{
  background-color: rgb(250, 250, 250);
}
*{
    font-family: 'Raleway', sans-serif;
}
.container{
    display: flex;
    flex-direction: row;
    text-align: right;
    justify-content: space-between;
}
.headings{
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 2%;
    position: relative;
    top: 120px; 
}
.headings h1{
    font-weight: bolder;
    font-size: 3rem;
}
.input:focus {
    outline: none;
    color: dodgerblue;
}

.input input{
    border: none;
    outline: none;
    padding: 0;
    background-color: #108dcc;
    font-family: 'Readex Pro', sans-serif;
    width: 25.5ch;
    text-transform: uppercase;
    letter-spacing: 0.5ch;
    font-size: 2em;
    background: repeating-linear-gradient(90deg, dimgrey 0, dimgrey 1ch, transparent 0, transparent 1.5ch) 0 100%/ 25ch 2px no-repeat;
}
.input button{
    border: none;
    width: 40%;
    padding: 2% 5% 2% 5%;
    background-color: #108dcc;
    color: #fff;
    font-size: 16px;
    font-weight: bold;
    margin: 2% 0 0 2%;
    border-radius: 100px;
}
.input button:hover{
    transition: 0.3s;
    background-color: #097db8;
}
.links a{
    color: #222;
    font-size: 0.6em;
    text-decoration: none;
    display: flex;
    flex-direction: column;
    margin-top: 1%;
    position: relative;
    top: 20px;
}
.links a:hover{
    text-decoration: underline;
}
.img{
    position: relative;
    top: 150px;
    /* animation: up-down 2s ease-in-out infinite alternate-reverse both; */
    width: 320px;
    height: 320px;
    animation-name: rotate;
    animation-duration: 5s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    border-radius: 50%;
}
@keyframes rotate{
    from{ transform: rotate(-360deg); }
    to{ transform: rotate(360deg); }
}
