.innerContainer{
    background-color: #fff;
    height: 90vh;
    padding: 5%;
    position: relative;
    top: 250px;
    text-align: center;

}
.innerContainer h2{
    color: #000;
    word-spacing: 0.2em;
    letter-spacing: 0.8em;
    margin-bottom: 10%;
    text-transform: uppercase;
    font-weight: bold;
}
.contents {
    display: flex;
    justify-content: space-around;
    cursor: pointer;
}
.contents p{
    color: #000;
    font-weight: bold;
    margin-top: 5%;
}
#innerContainer{
    background-image: linear-gradient(to left, #111, #108dcc);
}
#innerContainer h2{
    margin-bottom: 2%;
    color: #fff;
}
#innerContainer h5{
    font-family: 'Raleway', sans-serif;
    color: #000;
    font-weight: bold;
}
#contents{
    width: 100%;
    height: 90vh;
}
#contents p{
    color: #eee;
    font-size: 0.6em;
}
#contents div{
    padding: 4%;
}
#phone_container{
    display: flex;
    justify-content: space-around;
}
#phone_container h5{
    text-align: right;
    font-family: 'Raleway', sans-serif;
    max-width: 60rem;
}
#h4{
    text-align: right;
    font-family: 'Readex Pro', sans-serif;
}
#button{
    position: relative;
    top: 50px;
    right: -1000px;
    color: #eee;
    background-color: #108dcc;
    padding: 1% 4% 1% 4%;
    box-shadow: 5px 5px 10px #222;
    font-size: .8em;
    border-radius: 100px;
    text-decoration: none;
}
#button:hover{
    background-color: #097db8;
}

.heading{
    display: flex;
    justify-content: center;
    margin-top: 5%;
}
.heading div{
    text-align: center;
    margin: 2%;
    width: 50%;
    border-radius: 100px;
    border: none;
    box-shadow: 5px 5px 10px #000; 
}
.heading span{
    font-size: 25px;
    background-color: #42cfca;
    padding: 0% 4% 0% 4%;
    border-radius: 100px;
    box-shadow: 5px 5px 10px #42cfca;
    color: #fff;
    margin: 5%;
}
.heading h3{
    font-family: 'Readex Pro', sans-serif;
    text-shadow: 5px 5px 10px #42cfca;
    letter-spacing: 8pt;
    animation: flash;
    animation-duration: 1s;
}
.title{
    text-align: center;
    letter-spacing: 5pt; 
}
.upperBar{
    color: #fff;
    font-size: 2em;
    background-color: #000;
    padding: 1%;
}
.table{
    width: 100%;
    background-color: #c7fffd;
}
.table th{
    padding: 1%;
    font-family: 'Readex Pro', sans-serif;
    text-shadow: 5px 5px 10px #42cfca;
    letter-spacing: 8pt;
}
.table td{
    font-size: 1em;
    font-family: 'Quicksand', sans-serif;
}
